import React from 'react';
import './Navigation.css';

const Navigation = ({onRouteChange, isSignedIn}) => {
    if (isSignedIn === true) {
        return (
            <nav>
                <p onClick={()=>onRouteChange('signin')} className='f3 pa3 dim underline pointer link'>Sign Out</p>
            </nav>
        );
    } else {
        return (
            <nav>
                <p onClick={()=>onRouteChange('signin')} className='f3 pa3 dim underline pointer link'>Sign In</p>
                <p onClick={()=>onRouteChange('register')} className='f3 pa3 dim underline pointer link'>Sign Up</p>
            </nav>
        );
    }
    
}

export default Navigation;