import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import Rank from './components/Rank/Rank';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';
import ImageUrl from './components/ImageUrl/ImageUrl';
import Facerecognition from './components/Facerecognition/Facerecognition';
import Particles from 'react-particles-js';
import './App.css';
import 'tachyons';
import Clarifai from 'clarifai';

const particleParams = {
  "particles": {
    "number": {
      "value": 44,
      "density": {
        "enable": true,
        "value_area": 236.7442924896818
      }
    }
  }
}

const app = new Clarifai.App({
  apiKey: '7c5ee073030641dbbac84beb7488bc8e'
 });

class App extends Component {

  constructor() {
    super();
    this.state = {
      input: '',
      box:  {},
      route: 'signin',
      isSignedIn: false,
    }
  }


  onRouteChange = (route) => {
    if (route === 'home')  {
      this.setState({isSignedIn: true})
    } else {
      this.setState({isSignedIn: false})
    }
    this.setState({route: route});
  }

  onInputChange = (event) => {
    this.setState({input: event.target.value});
  }

  faceboxCalculation = (response) => {
    const image = document.getElementById('faceImg');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      left_col: response.left_col * width,
      right_col: width - (response.right_col * width),
      top_row: response.top_row * height,
      bottom_row: height - (response.bottom_row * height),
    }
  }

  setBoxState = (box) => {
    this.setState({box: box});
  }

  onSubmit = () => {
    console.log('click');
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
    .then(response =>
      this.setBoxState(this.faceboxCalculation(response.outputs[0].data.regions[0].region_info.bounding_box)))
    .catch(err => console.log(err))
    }

  render() {
    const { isSignedIn, box, input } = this.state;
    return (
      <div className="App">
        <Particles className='particles' params={particleParams}/>
        <Navigation isSignedIn={isSignedIn} onRouteChange={this.onRouteChange}/>
        {
          this.state.route === 'home' ?
          <div>
            <Logo />
            <Rank />
            <ImageUrl onInputChange={this.onInputChange} onSubmit={this.onSubmit}/>
            <Facerecognition box={box} input={input}/>
          </div>
          : (
            this.state.route === 'signin' ?
            <SignIn onRouteChange={this.onRouteChange}/>
            :
            <Register onRouteChange={this.onRouteChange}/>
          )
        }
        
      </div>
    );
  }
}

export default App;
