import React from 'react';
import './ImageUrl.css';

const ImageUrl = ({onInputChange, onSubmit}) => {
    return (
        <div>
            <p className='f3'>
            { 'This is a revolutionary application that will detect faces for you..' }
            </p>
            <div className='center'>
                <div className='center pa4 br3 shadow-5 form'>
                    <input className='f3 pa2 w-70 center' type='text' onChange={onInputChange}/>
                    <button className='w-30 grow link f4 ph3 pv2 dib white bg-dark-green bw0' onClick={onSubmit}>Detect</button>
                </div>
            </div>
        </div>
    );
}

export default ImageUrl;